$grey: #b3b3b3;
$white: #fff;
$red: #e50914;
$red-error: #ad181f;
$black: #330;

.update-profile-view-container input:-webkit-autofill,
.update-profile-view-container input:-webkit-autofill:hover,
.update-profile-view-container input:-webkit-autofill:focus {
  border: 1px solid $grey;
  -webkit-text-fill-color: $black;
  -webkit-box-shadow: 0 0 0px 1000px $white inset;
  transition: background-color 5000s ease-in-out 0s;
}

.update-profile-view-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  min-height: 820px;
  background-color: $white;

  &:last-child {
    align-items: center;
  }
}

div.update-profile {
  width: 100vw;
  display: flex;
  justify-content: center;
  padding-top: 50px;

  &-card {
    min-width: 375px;
    border: none;

    &-title {
      font-size: 2.2rem;
      font-weight: 600;
    }
  }
}

.update-profile-input-group {
  position: relative;
  z-index: 10;
  margin-left: 0px;
  margin-right: 0px;
}

.update-profile-input-group.horizontal-group {
  display: flex;
  justify-content: space-between;
}

label.update-profile-input-label {
  position: absolute;
  z-index: 150;
  color: $black;
  font-size: 1rem;
  top: 20px;
  left: 18px;
  transition: all 300ms ease-in;
}

input.update-profile-input {
  width: 100%;
  position: relative;
  z-index: 100;
  border-radius: 2px;
  border: 1px solid $grey;
  opacity: 1;
  font-size: 1rem;
  line-height: 50px;
  padding: 12px 18px 0;
}

.col.horizontal-input {
  padding-left: 0;
  padding-right: 0;

  &.left {
    margin-right: 4px;
  }
  &.right {
    margin-left: 4px;
  }
}

input.update-profile-input:focus + label.update-profile-input-label,
input.update-profile-input:valid + label.update-profile-input-label,
input.update-profile-input.focus + label.update-profile-input-label {
  top: 5px;
  font-size: 0.7rem;
}

.input-error {
  color: $red-error;
  font-size: 0.8rem;
  margin-left: 3px;
}

.update-button {
  margin: 24px 0 12px;
  width: 100%;
  padding: 16px;
  color: $white;
  background: $red;
  font-size: 0.9rem;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}

.delete-account {
  font-size: 0.8rem;
}
